.title{
    font-size: 18px;
    color: var(--subtitle);
    margin-bottom: 20px;
    font-weight: 700;
}
.icons{
    margin-bottom: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.add__btn{
    color: #273240;
    padding: 8px 24px;
}
.add__category-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}
.add__category-item input{
    border: 1px solid #273240;
    padding: 6px 10px;
    width: 240px;
    font-size: 16px;
    color: #273240;
}
.add__category-item button{
    margin-right: auto;
}