.wrapper{
    padding: 50px 80px;
    height: 100%;   
    width: 100%;
}
.add__form{
    height: 100%;
    display: flex;
    flex-direction: column;
}
.add__form-actions{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    gap: 8px;
    justify-content: center;
}
.add__form-btn{
    background-color: #e3dede;
    color: #262A41;
    border-radius: 5px;
}
.add__form-btn.active{
    background-color: #157AFF;
    color: #fff;
}
.add__form-amount {
    text-align: center;
    width: 160px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 20px;
}
.add__form-acount{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
}
.add__form-acount h3{
    font-weight: 600;
}
.add__form-plus {
    width: 34px;
    height: 34px;
    background-color: transparent;
}
.add__form-plus svg path {
    stroke: var(--subtitle);
}
.add__account{
    max-width: 300px;
}
.add__account{
    margin-bottom: 20px;
}
.add__form-date{
    max-width: 300px;
    margin-bottom: 20px;
}
.add__form-description{
    max-width: 300px;
    margin-bottom: 30px;
}
.success{
    margin-top: 20px;
    font-size: 16px;
    font-weight: 600;
}