.container {
    height: 100%;
    padding: 20px;
}
.wrapper{
    background-color: var(--page-background);
    color: var(--text);
    width: 100%;
    height: 100%;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    height: 100%;
    box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.4);
}