.settings{
    padding: 50px 80px;
    height: 100%;
    width: 100%;
}
.title{
    font-size: 18px;
    font-weight: 600;
    color: var(--subtitle);
    margin-bottom: 15px;
    transition: color .3s ease;
}
.accounts {
    margin-bottom: 15px;
}
.accounts__list{
    max-width: fit-content;
    margin-bottom: 15px;
}
.accounts__list-item{
    display: flex;
    gap: 8px;
    align-items: center;
    width: 100%;
}
.accounts__list-text{
    font-size: 16px;
    color: var(--subtitle);
    font-weight: 400;
    width: 100%;
    display: block;
    transition: color .3s ease;
}
.accounts__list-item:not(:last-child) {
    margin-bottom: 12px;
}
.add__account{
    max-width: 300px;
    margin-bottom: 15px;
}