.item{
    color: var(--statistic-text);
    font-size: 13px;
    transition: all .3s ease;
}
.item:not(:last-child){
    margin-bottom: 15px;
}
.item__info{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.item__info-title{
    font-weight: 500;
}
.item__info-amount{
    font-weight: 500;
}
.item__info-amount span{
    font-size: 11px;
    margin-left: 5px;
}
.item__bar{
    width: 100%;
    height: 5px;
    background-color: #ECEFF5;
    border-radius: 5px;
}
.item__bar-fill{
    height: 100%;
    border-radius: 5px;
    background-color: #31BA96;   
}