.add__category{
    margin-bottom: 20px;
}
.add__category-title{
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 20px;
    color: var(--subtitle);
    transition: color .3s ease;
}
.add__category-items{
    display: grid;
    grid-template-columns: repeat(auto-fill, 100px);
    grid-gap: 20px;
    margin-bottom: 20px;
}