.button{
    border: 1px solid rgba(0,0,0, .1); 
    border-radius: 30px;
    position: relative;
    display: flex;
    background-color: #fff;
    width: 80px;
    height: 40px;
    border-radius: 120px;
    box-shadow: inset 0 5px 12px rgba(0, 0, 0, .1), inset 0 1px 7px rgba(0, 0, 0, .1), inset 0 -2px 2px rgba(0, 0, 0, .1);
    position: relative;
    padding:  0 2px;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.switch {
    background-color: #fff;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    box-shadow:  0 3px 15px rgba(0, 0, 0, .2);
    transition: all .4s ease;
}

.icon__container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon__container img{
    width: 32px;
    height: 32px;
}
.darkmode .switch{
    transform: translateX(37px);
    background-color: #2b2b2b;
}