.select{
    position: relative;
    width: fit-content;
}
.select__text{
    font-size: 16px;
    cursor: pointer;
    display: flex;
    gap: 8px;
    align-items: center;
    color: var(--subtitle);
    font-weight: 400;
    font-size: 16px;
    transition: all .3s ease;
}
.select__text h3{
    font-weight: 400;
    font-size: 16px;
}
.select__text.selected {
    margin-bottom: 5px;
}
.select__btn{
    width: 20px;
    height: 14px;
    transition: all .3s ease;
}
.select__btn svg path{
    fill: var(--subtitle);
    transition: fill .3s ease;
}
.select__text.selected .select__btn{
    transform: scale(1, -1);
    bottom: 5px;
}
.select__list{
    position: absolute;
    min-width: 100%;
    background-color: var(--background-color);
    border: 1px solid #979797;
    font-size: 16px;
    animation: falling 0.8s ease-in-out;
    z-index: 2;
}
.select__list-option{
    cursor: pointer;
    padding: 8px 15px;
    transition: all .3s ease;
    color: var(--subtitle);
    transition: all .3s ease;
}
.select__list-option:hover{
    background-color: var(--pluse-btn-hover);
    color: #fff;
}
.select__list-option:not(:last-child){
    border-bottom: 1px solid #979797;
}
@keyframes falling {
    0% {opacity: 0;
        transform: translateY(-10px)}
    100% {
        opacity: 1;
        transform: translateY(0)
    }   
}