.transaction{
    margin-bottom: 22px;
}
.transaction__top{
    padding: 8px 0;   
    margin-bottom: 24px;
}
.transaction__content{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.transaction__content-info{
    width: 100%;
    margin-left: 24px;
}
.transaction__content-category{
    color: var(--category);
    font-size: 16px;
    margin-bottom: 5px;
    font-weight: 700;
    transition: color .3s ease;
}
.transaction__content-descr{
    display: flex;
    color: #404852;
}
.transaction__content-time{
    padding-right: 10px;
}
.transaction__content-text{
    position: relative;
    color: var(--description);
    transition: color .3s ease;
}
.transaction__content-amount{
    margin-left: 24px;
    font-size: 16px;
    font-weight: 600;
    transition: color .3s ease;
    color: var(--category);
    display: flex;
    gap: 10px;
    align-items: center;
    font-weight: 600;
    align-items: center;
}
.transaction__content-amount span{   
    font-size: 12px;
}
.account{
    color: var(--category);
    padding-left: 15px;
    display: flex;
    font-weight: 400;
    transition: color .3s ease;
}
.transaction__content-sum{
    display: flex;
    align-items: flex-start;
    gap: 5px;
}