.bar{
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items: flex-end;
    gap: 6px;
    margin-bottom: 35px;
}
.hasTransactions{
    background-color: #157AFF;
    width: 16px;
}
.noTransactions{
    background-color: #979797;
    width: 16px;
    height: 30px;
}