.button{
    padding: 6px 12px;
    border-radius: 5px;
    background-color: #157AFF;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    display: inline-block;
    width: fit-content;
    transition: all .3s ease;
}
.button__custom{
    padding: 8px 16px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    display: inline-block;
    width: fit-content;
    transition: all .3s ease;
}
.button:hover,
.button:focus,
.button__custom:hover,
.button__custom:focus
{
    background-color: rgba(21, 122, 255, .8);
    color: #fff;
    box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.5);
}
.button:disabled,
.button:disabled:hover
{
    background-color: rgba(21, 122, 255, .6);
    box-shadow: none;
}