*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none; }
a:focus,a:active{outline: none; color: inherit;}

nav,footer,header,aside{display: block;}

html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none; color: inherit; }
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit; font-weight: inherit;}

body{
	font-family: "Poppins", sans-serif;
	font-weight: 600;
	line-height: 1.2;
	--background-color: #e3dede;
	--sidebar-text-color: #262A41;
	--sidebar-active-link: #157AFF;
	--page-background: #fff;  
	--title-color: #262A41;
	--pluse-btn-hover: #157AFF;
	--subtitle: #262A41;
	--category: #262A41;
	--description: #404852;
	--item-border: #DEDEDE;
	--statistic: #F9FAFC;
	--statistic-text: #262A41;
	--input-bg-color: #e3dede;
	background-color: var(--background-color);
	transition: all .3s ease;
}
body.dark{
	--background-color: #212327;
  	--sidebar-text-color: #fafcfb;
	--page-background: #27292E;
	--title-color: #c3daf7;
	--pluse-btn-hover: rgba(21, 122, 255, .7);
	--subtitle: #fff;
	--category: #c3daf7;
	--description: #8c95a3;
	--item-border: #404852;
	--statistic: #7a7777;
	--statistic-text: #fff;
	--input-bg-color: #fff;
}
#root{
	height: 100%;
	width: 100%;
}
a.active-link{
        color: var(--sidebar-active-link);
}
.container{
	width: 100%;
	min-height: 100%;
	height: 100%;
}
.wrapper{
	display: grid;
	grid-template-columns: 310px auto;
	height: 100%;
}
.content{
	width: 100%;
}
.error__message {
	padding-top: 15px;
	font-size: 16px;
	font-weight: 600;
	color: red;	
}