.item{
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    cursor: pointer;
    max-width: 100%;
}
.item__title{
    font-size: 14px;
    color: var(--subtitle);
    font-weight: 400;
    flex: 1 0 auto;
    text-align: center;
    align-content: space-around;
    background-color: var(--background-color);
    transition: all .3s ease;
    width: 100%;
    border-radius: 2px;
    padding: 5px;
    overflow-x: hidden;
    border: 1px solid rgba(0, 0, 0, .3);
}