.content{
    padding: 50px 80px;
    width: 100%;
}
.content__top{
    margin-bottom: 22px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.title{   
    font-size: 40px;
    color: var(--title-color);
    transition: color .3s ease;
}
.content__top-link{
    display: flex;
    gap: 12px;
    align-items: center;
    transition: all .3s ease;
}
.content__top-link svg path {
    fill: var(--title-color);
}
.content__top-link span{
    color: var(--title-color);
}
.content__top-link:hover span{
    color: var(--pluse-btn-hover);
}
.content__top-link:hover svg path {
    fill: var(--pluse-btn-hover);
}
.content__top-link span{
    font-size: 20px;
}