.sidebar{ 
    height: 100%;
    position: relative;
}
.sidebar__wrapper{
    padding: 80px 10px 60px 30px;
    height: 100vh;
    width: 310px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--background-color);
    color: var(--sidebar-text-color);    
    transition: all .3s ease;
}
.sidebar__list li:not(:last-child) {
    margin-bottom: 20px;
}
.sidebar__list li a{
    transition: color .3s ease;
    font-size: 24px;
    transition: color .3s ease;
    cursor: pointer;
}
.sidebar__list li a:hover,
.sidebar__list li a:focus {
    color: var(--sidebar-active-link);
}
.theme__switch{
    display: flex;
    justify-content: center;
}